  left: -300px;
  min-height: 100vh;
  text-align: center;
  -webkit-transition: all 400ms;
  transition: all 400ms;
  width: 300px;
}

.NavBar__open {
  left: 0;
}

.NavBar__open::after {
  background: rgba(137, 210, 188, .4);
  content: '';
  display: block;
  position: absolute;
  right: -8px;
  top: 0;
  height: 100vh;
  width: 8px;
}

.NavBar__profile-image {
