}

.NavBar__menu {

}

.NavBar__menu-item {
  border-bottom: solid 1px transparent;
  cursor: pointer;
  margin-bottom: 5px;
  text-transform: uppercase;
  -webkit-transition: all 400ms;
  transition: all 400ms;
}

.NavBar__menu-item-link {
  color: rgba(0, 0, 0, .7);
  -webkit-transition: all 400ms;
  transition: all 400ms;
}

.NavBar__menu-item-link,
.NavBar__menu-item-link:hover {
  text-decoration: none;
}

.NavBar__menu-item-link:hover {
  color: #007bff;
}

.NavBar__menu-item:hover {
  color: rgb(26, 158, 214);
  border-color: rgb(26, 158, 214);
}


/*
  ********************
  Close icon
  ********************
*/
.NavBar__cross {
  background: rgba(137, 210, 188, .8);
  border-radius: 4px;
  cursor: pointer;
  padding: 5px;
  position: absolute;
  height: 40px;
  top: 20px;
  right: -60px;
  width: 40px;
}

.NavBar__cross,
.NavBar__cross-icons {
  -webkit-transition: all 400ms;
  transition: all 400ms;
}

.NavBar__cross-open {
  background: rgba(137, 210, 188, .4);
}

.NavBar__cross-open .NavBar__cross-icons {
  background: none;
}

.NavBar__cross-open .NavBar__cross-icons::after {
  top: 10px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.NavBar__cross-open .NavBar__cross-icons::before {
  top: 10px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.NavBar__cross-icons {
  background-color: black;
  border-radius: 4px;
  display: block;
  height: 2px;
  margin-top: 5px;
  width: 100%;
}

.NavBar__cross-icons::after,
.NavBar__cross-icons::before {
  background-color: black;
  border-radius: 4px;
  content: '';
  display: block;
  height: 2px;
  margin-top: 10px;
  position: absolute;
  width: 30px;
}

.NavBar__cross-icons::after {
  top: 10px;
}

.NavBar__cross-icons::before {
  top: 20px;
}


/*
  ******************************
  desktop styles
  ******************************
*/
@media only screen and (min-width: 600px) {
